import React from 'react'
import ic_01 from '../Assets/images/ic_01.png'
import ic_02 from '../Assets/images/ic_02.png'
import ic_03 from '../Assets/images/ic_03.png'
import ModalRecoverNote from './ModalRecoverNote'
import ModelAccountSetup from './ModelAccountSetup'

function NoteAccount() {
  return (
    <>
        <section className="mainSection">
            <div className="container">
                <div className="row">
                    <div className="col-12 text-center">
                        <h2>Note Account</h2>
                    </div>
                    <div className="col-12">
                        {/* <div id="noteMessage" className="collapse show noteMessage mt-3 mb-3">
                            <button type="button" className="btnClose" data-toggle="collapse" data-target="#noteMessage"><i className="fa-solid fa-xmark"></i></button>
                            <p>Leverage agile frameworks to provide a robust synopsis for high level overviews. Iterative approaches to corporate strategy foster collaborative thinking to further the overall value proposition.</p>
                        </div> */}
                    </div>
                    <div className="col-12 ptb_20">
                        <div className="card">
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <h6>Note account : -</h6>
                                </div>
                                <div className="col-12 col-md-6">
                                    <h6>Backed up with : -</h6>
                                </div>
                            </div>
                            <div className="row pt-4">
                                <div className="col-12 col-md-4 mt-3">
                                    <div className="NA_card">
                                        <div className="NA_ic">
                                            <img src={ic_01} alt='ic_01'/>
                                        </div>
                                        <div className="NA_text">
                                            <p>Setup Note Account for storing encrypted notes on Ethereum blockchain</p>
                                            <button type="button" className="btn" data-toggle="modal" data-target="#modalAccountSetup">Setup Note</button>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-12 col-md-4 mt-3">
                                    <div className="NA_card">
                                        <div className="NA_ic">
                                            <img src={ic_02} alt="ic_02"/>
                                        </div>
                                        <div className="NA_text">
                                            <p>Recover Note Account associated with this Web3 wallet</p>
                                            <button type="button" className="btn2"  data-toggle="modal" data-target="#ModalRecoverNote">Recover Note</button>
                                            
                                        </div>
                                    </div>
                                </div>
                                <div className="col-12 col-md-4 mt-3">
                                    <div className="NA_card">
                                        <div className="NA_ic">
                                            <img src={ic_03} alt="ic_03"/>
                                        </div>
                                        <div className="NA_text">
                                            <p>Leverage agile frameworks to provide a robust synopsis.</p>
                                            <button type="button" className="btn">KYC</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <ModelAccountSetup/>
            <ModalRecoverNote/>
        </section> 
    </>
  )
}

export default NoteAccount