import React from 'react'

function ModelAccountSetup() {
  return (
    <>
        {/* <!-- The Modal --> */}
        <div className="modal fade" id="modalAccountSetup">
        <div className="modal-dialog">
            <div className="modal-content">

            {/* <!-- Modal Header --> */}
            <div className="modal-header">
                <h4 className="modal-title">Note setup</h4>
                <button type="button" className="close" data-dismiss="modal"></button>
            </div>

            {/* <!-- Modal body --> */}
            <div className="modal-body">
                <p>Leverage agile frameworks to provide a robust synopsis for high level overviews. Iterative approaches to corporate strategy foster collaborative thinking to further the overall value proposition.</p>
                <br/>
                <h6 className="mb-0">Note Account key</h6>
                <div className="code__box">
                    <input type="text"></input>
                    <div className="">
                        <span>Copy</span>
                        <button type="button" className="icBtn_"><i className="fa-regular fa-copy"></i></button> 
                    </div>
                </div>

                <div className="card noteCard mt-3">
                    Leverage agile frameworks to provide a robust synopsis for high level overviews. 
                </div>
                <div className="form-group form-check pt-3">
                    <label className="form-check-label">
                    <input className="form-check-input" type="checkbox"/> Leverage agile frameworks to provide.
                    </label>
                </div>

                <div className="">
                    <button type="button" className="btn btnFull">Setup Note</button>
                </div>
                      </div>
                  </div>
              </div>
              </div>
        </>
    )
}

export default ModelAccountSetup