import React from 'react'
import Dropdown from 'react-bootstrap/Dropdown';

import Icons8Sun from '../Assets/images/icons8-sun.svg';
import MenuIcon from '../Assets/images/icons8-squared_menu.svg'
import FoundationLogoM from '../Assets/images/foundation-logo-mobile.png';
import HomeIcon from '../Assets/images/house-solid.svg';
import FoundationLogo from '../Assets/images/foundation-logo.png';
import FoundationLogoWhite from '../Assets/images/white-theme/foundation-logo-white.png';
import { ConnectButton } from '@rainbow-me/rainbowkit';

class Navbar extends React.Component {

    handleModeTrigger = (event) => {
        this.props.modeChange(event);
        event.preventDefault();
    }

    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-sm">
                    <a href={"/"} className="navbar-brand header-logo">
                        <img src={FoundationLogoM} alt="FoundationLogo" className="large-icon mobile" />
                        <img src={FoundationLogo} alt="FoundationLogo" className="large-icon dark-theme" />
                        <img src={FoundationLogoWhite} alt="FoundationLogo" className="white-theme" />
                    </a>
                    {/* <button className="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navb">
                            Menu
                        </button> */}


                    <ul className="navbar-nav ml-auto navbar-nav navbar-navRight" id="navb">
                        <li className="nav-item">

                        </li>
                        <li className="nav-item">
                            <Dropdown>
                                <Dropdown.Toggle variant="success" id="dropdown-basic">
                                    <img src={MenuIcon} />
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <Dropdown.Item href="https://foundationtoken.io/">Home</Dropdown.Item>
                                    <Dropdown.Item href="/noteaccount">Note Account</Dropdown.Item>

                                </Dropdown.Menu>
                            </Dropdown>
                        </li>

                        <li className="nav-item">
                            <ConnectButton type="button" className="btn-green" ></ConnectButton>
                        </li>
                        <li className="nav-item">
                            <button type="button" className="btn btnIc" onClick={this.handleModeTrigger}>
                                <img src={Icons8Sun} alt="Icons8Sun" />
                            </button>
                        </li>
                    </ul>


                </nav>
            </div>
        )
    }
}

export default Navbar
