import React from 'react'

function ModalDeposit() {
    return (
        <>
            {/* <!-- The Modal --> */}
            <div className="modal fade" id="modalDeposit">
                <div className="modal-dialog">
                    <div className="modal-content">

                {/* <!-- Modal Header --> */}
                <div className="modal-header">
                    <h4 className="modal-title">Your private note</h4>
                    <button type="button" className="close" data-dismiss="modal"></button>
                </div>

                {/* <!-- Modal body --> */}
                <div className="modal-body">
                    <p>Please backup your note. You will need it later to withdraw your deposit back. Treat your note as aprivate key - never share it with anyone, including Foundation cash, devellopers.</p>
                    <div className="code__box">
                        <input type="text" ></input>
                        <div className="">
                            <span></span>
                            <button type="button" className="icBtn_"><i className="fa-regular fa-copy"></i></button> 
                            <button type="button" className="icBtn_"><i className="fa-solid fa-link"></i></button>
                        </div>

                        {/* <!-- Modal body --> */}
                        <div className="modal-body">
                            <p>Please backup your note. You will need it later to withdraw your deposit back. Treat your note as aprivate key - never share it with anyone, including Foundation cash.</p>
                            <div className="code__box">
                                <input type="text" name="" id="" />
                                <div className="">
                                    <span></span>
                                    <button type="button" className="icBtn_"><i className="fa-regular fa-copy"></i></button>
                                    <button type="button" className="icBtn_"><i className="fa-solid fa-link"></i></button>
                                </div>
                            </div>
                            <p>
                                The browser wil ask to save yournote as a file: backup-foundation-eth-xxxxx.txt
                            </p>
                            <br />
                            <h6>Gas Price</h6>
                            <div className="rangeStepRow">
                                <input type="radio" name="rad" id="a" checked />
                                <input type="radio" name="rad" id="b" />
                                <input type="radio" name="rad" id="c" />
                                <div className="slider">
                                    <div className="marker"></div>
                                </div>
                                <div className="label_Row">
                                    <label className="label_ text-left" for="a">Slow</label>
                                    <label className="label_" for="b">Standard</label>
                                    <label className="label_ text-right" for="c">Fast</label>
                                </div>
                            </div>

                            <div className="card noteCard mt-3">
                                You can also save encrypted notes on chain by setting upthe Note Account. create one on the account page.
                            </div>
                            <div className="form-group form-check pt-3">
                                <label className="form-check-label">
                                    <input className="form-check-input" type="checkbox" /> I backed up the note
                                </label>
                            </div>

                            <div className="">
                                <button type="button" className="btn btnFull">Send Deposit</button>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
                </div>
                </div>
            </>
    )
}

export default ModalDeposit