import React from 'react'
import Navbar from '../Components/Navbar';

import Footer from '../Components/Footer';
import Home from './Home';


import {
  BrowserRouter,
  Routes,
  Route,
  Switch
} from "react-router-dom";
import NoteAccount from './NoteAccount';

class Layout extends React.Component {

  state = {
    mode: false
  }

  handleHomePageMode = (event) => {
    this.setState({ mode: !this.state.mode })
  }


  render() {


    return (
      <div className={!this.state.mode ? "" : "foundation-white-theme"}>
        <div class="background">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
        <header className=" fixed-top foundation-header " >
          <Navbar modeChange={this.handleHomePageMode} />
        </header>

        <section className="mainSection">
          <BrowserRouter>

            <Routes >
             
             
              <Route path="/" element={<Home />} />
              <Route path="/noteaccount" element={<NoteAccount/>} />
              

            </Routes>
          </BrowserRouter>
        </section>
        <Footer />

      </div>
    )
  }
}

export default Layout
